.Courses_detail_page {
  .popular {
    .bg_cover {
      padding: 0;
    }
  }
}
.courses_detail {
  padding: 80px 0;
  .course_info {
    .author {
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      .img {
        width: 30px;
        height: 30px;
        border-radius: 30px;
        margin-right: 5px;
        display: inline-block;
        background-position: center;
        background-size: cover;
      }
      p {
        display: inline-block;
        font-size: 14px;
        margin-bottom: 5px;
        span {
          color: $mainColor;
          text-decoration: none;
        }
      }
    }
    .tag {
      background-color: #7c4bc0;
      display: inline-block;
      margin-left: 20px;
      p {
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 400;
        margin: 0;
        color: #fff;
        padding: 3px 8px;
        a {
          text-decoration: none;
          color: #fff;
        }
      }
    }
    .rating {
      color: #233f52;
      font-weight: 600;
      margin: 0;
      padding: 0px 0 20px;
      .star {
        background-color: transparent;
        font-weight: 400;
        font-size: 16px;
        i {
          color: #f16101;
        }
        margin: 0;
        padding: 0 10px 0 0;
      }
      span.rating-count {
        background-color: $lightBackground;
        color: #81868a;
        font-weight: 400;
        font-size: 14px;
        margin: 0 0 0 10px;
        padding: 3px 8px;
      }
    }
    .course_name {
      font-weight: 700;
      font-size: 32px;
      color: $headingColor;
      a {
        color: #012237;
        text-decoration: none;
      }
    }
  }
  .sources {
    .video_sec {
      margin-bottom: 25px;
      position: relative;
      img {
        width: 100%;
      }
      .wish_list {
        position: absolute;
        top: 20px;
        right: 25px;
        i {
          color: #fff;
          transition: 0.3s all;
        }
        &:hover {
          i {
            color: red;
          }
        }
      }
    }
  }
  #CoursesTab {
    border: 0;
    justify-content: center;
    li {
      a {
        border: 0;
        padding: 15px 47px;
        color: #81868a;
        border-radius: 0;
        transition: 0.3s all;
        background-color: #f1f1f1;
        position: relative;
        &.active {
          color: $whiteColor;
          background-color: $mainColor;
          &:after {
            content: "";
            bottom: -9px;
            position: absolute;
            left: 0%;
            right: 0%;
            display: block;
            margin: 0 auto;
            width: 0;
            border-left: 20px solid transparent;
            border-right: 20px solid transparent;
            border-top: 15px solid $mainColor;
            z-index: 1;
          }
        }
      }
    }
  }
  div#CoursesTabContent {
    border: 1px solid #f1f1f1;
    margin-top: 1px;
    .overview {
      padding: 40px 30px;
      .links {
        margin: 15px 0;
        font-weight: 500;
        a {
          color: $mainColor;
          text-decoration: none;
        }
      }
    }
    .curriculum {
      padding: 40px 0px;
    }
  }
  #ChapterOfCourses {
    .card {
      border-radius: 0;
      border: 0;
      margin-bottom: 20px;
      &.active {
        a.price {
          background: $whiteColor;
          color: $mainColor;
        }

        & .card-header button {
          background-color: $mainColor !important;
          color: $whiteColor;
        }
      }
      .card-header {
        $parent: &;
        border-radius: 0;
        border: 0;
        padding: 0;
        h2 {
          position: relative;
          a {
            position: absolute;
            right: 15px;
            top: 15px;
            padding: 5px 20px;
            font-size: 14px;
            transition: 0.3s all;
            background: $mainColor;
            border-radius: 25px;
            color: $whiteColor;
            text-decoration: none;
          }
          /*                    &>a button[aria-expanded=true]{
                                            
                                        }*/
        }

        @at-root #{$parent} {
          & button[aria-expanded="true"] {
            background-color: $mainColor !important;
            color: $whiteColor;
          }
        }
        button {
          font-size: 16px;
          font-weight: 600;
          line-height: 22px;
          transition: 0.3s all;
          padding: 5px 170px 5px 50px;
          color: $headingColor;
          position: relative;
          border-radius: 0;
          &[aria-expanded="true"]:before {
            content: "";
            transition: 0.3s all;
          }
          span {
            display: block;
          }
          &:focus {
            box-shadow: none;
            outline: 0;
          }
          &:before {
            top: 27%;
            transition: 0.3s all;
            position: absolute;
            left: 16px;
            content: "";
            font-family: "Font Awesome 5 Pro";
            font-weight: 900;
          }
        }
      }
      .card-body {
        .course_detail {
          margin: 20px 0;
          tr {
            &:first-child {
              th,
              td {
                border: 0;
              }
            }
            &:last-child {
              th,
              td {
                border-bottom: 1px solid #dee2e6;
              }
            }
            th {
              width: 75%;
              color: heading;
              font-weight: 600;
              &:first-child {
                width: 5%;
                text-align: center;
                color: $mainColor;
                .far {
                  font-weight: 500;
                }
              }
            }
            td {
              width: 20%;
            }
          }
        }
      }
    }
  }
  .instructor {
    .card {
      border-radius: 0;
      margin-top: -1px;
      background: transparent;
      .card-body {
        .left_block {
          float: left;
          width: 30%;
          padding-right: 5px;
          img {
            width: 100%;
          }
        }
        .right_block {
          float: left;
          width: 70%;
          padding-top: 12px;
          padding-left: 5px;
          h5 {
            font-size: 25px;
            font-weight: 600;
            color: $headingColor;
            margin: 0;
          }
          .authority {
            color: $headingColor;
            text-transform: uppercase;
            font-weight: 400;
            margin: 0;
          }
          .social {
            width: 50%;
            margin: 0;
            .col-4 {
              padding: 0 5px;
            }
            a {
              margin: 0 auto;
              i {
                border-radius: 40px;
                transition: 0.3s all;
                background-color: #f1f1f1;

                padding: 10px;
                color: #81868a;
                &:hover {
                  background-color: #00abef;
                  color: #ffffff;
                }
              }
            }
          }
        }
      }
    }
  }
  .review {
    .review_score {
      padding: 40px 30px;
      border-bottom: 2px solid #f1f1f1;
      .circle {
        width: 100%;
        height: 100%;
        background-color: #f1f1f1;
        border-radius: 75%;
        display: flex;
        justify-content: center;
        align-items: center;
        .circle-body {
          text-align: center;
          h3 {
            color: $mainColor;
            font-size: 50px;
            font-weight: 600;
            margin-bottom: 0px;
          }
          p {
            color: #81868a;
            .star {
              font-size: 18px;
              display: block;
              i {
                color: #f16101;
              }
            }
          }
        }
      }
      .progress-label {
        float: left;
        margin-right: 1em;
        margin: 0px 10px 0;
        width: 18%;
      }
      .progress {
        height: 5px;
        margin-top: 10px;
        width: 70%;
        float: left;
      }
      .progress-percentage {
        float: left;
        width: 7%;
        text-align: right;
        color: $headingColor;
        font-weight: 600;
      }
      .progress-bar {
        position: relative;
        background-color: $mainColor;
        span {
          &:after {
            content: "";
            position: absolute;
            top: -5px;
            right: 0;
            width: 10px;
            height: 10px !important;
            background-color: $mainColor;
            border-radius: 50%;
            z-index: 5 !important;
          }
        }
      }
    }
    .posted_reviews {
      padding: 40px 30px;
      border-bottom: 2px solid #f1f1f1;
      .card {
        border: 0;
        .card-body {
          padding: 0 0 20px;
          .left_block {
            width: 20%;
            float: left;
            img {
              width: 100%;
            }
          }
          .right_block {
            width: 80%;
            padding-left: 10px;
            padding-top: 10px;
            float: left;
            h5 {
              color: $headingColor;
              font-size: 20px;
              font-weight: 600;
            }
            p {
              font-weight: 500;

              color: $mainColor;
              .star {
                padding-left: 10px;
                i {
                  color: #f16101;
                  &.ss {
                    color: #f1f1f1;
                  }
                }
              }
            }
          }
        }
      }
      p {
        font-size: 14px;
      }
    }
    .add_review {
      padding: 30px;
      h2 {
        color: $headingColor;
        font-weight: 700;
        margin-bottom: 10px;
      }
      p {
        color: #81868a;
        margin-bottom: 25px;
        .star {
          i {
            color: #f16101;
            &.ss {
              color: #f1f1f1;
            }
          }
        }
      }
      .content .contact_form {
        padding: 0px;
      }
      .more {
        padding: 13px 0;
        margin: 20px 0 0 0;
        text-align: center;
      }
    }
  }
  .forum {
    .create_thread {
      background: $mainColor;
      padding: 3px;
      border-radius: 3px;
      margin: 3px;

      textarea {
        border: none;
      }

      .hover_button {
        background-color: $mainColor;
        color: #fff;
        font-size: 18px;
        border: none;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: 200ms;
        border-radius: 3px;
        padding: 3px 4px !important;
        cursor: pointer;

        &:hover {
          color: $mainColor;
          background-color: #fff;
        }
        &:active {
          color: #fff;
          background-color: $mainColor;
        }
        &:focus {
          outline: 0;
        }
      }
    }
    .search_box {
      padding: 20px;
      display: flex;
      justify-content: space-between;
      .input-group {
        border: 1px solid #7d7f82;
        border-radius: 25px;
        input.form-control {
          border: 0;
          border-radius: 25px;
        }
      }
      .btn {
        color: #818386;
        &:focus {
          outline: 0;
          box-shadow: none;
        }
      }
    }

    .quiz_list {
      padding: 30px 20px;
      border-bottom: 2px solid #f1f1f1;
      h6 {
        text-align: center;
        color: $headingColor;
        font-size: 1.25rem;
        span {
          display: block;
        }
      }
      h5 {
        font-size: 18px;
        font-weight: 600;
      }
      img {
        width: 60%;
        margin: 0 auto 10px;
        display: block;
      }
      p {
        margin: 0;
        font-size: 14px;
        &.posted {
          text-align: center;
          font-size: 12px;
          span {
            font-size: 18px;
            font-weight: 600;
            color: $mainColor;
            display: block;
          }
        }
      }
    }
  }
  .course_price_table {
    .card {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 0 20px;
      border-radius: 0;
      background-color: #f1f1f1;
      border: 0;
      .card-body {
        padding: 40px 30px;

        text-align: center;
        span {
          color: #81868a;
          font-size: 14px;
          font-weight: 600;
          text-transform: uppercase;
        }
        h4 {
          color: $headingColor;
          font-weight: 700;
          font-size: 30px;
        }
        .more {
          width: auto;
          padding: 8px 30px;
          margin-bottom: 0;
        }
      }
    }
    ul {
      margin-bottom: 20px;
      li {
        border-color: #f1f1f1;
        font-size: 14px;
        color: #81868a;
        padding: 20px 1.25rem;
        text-align: left;
        i {
          color: $mainColor;
          padding: 0 10px 0 0;
        }
        span {
          font-weight: 600;
          padding: 0 0 0 10px;
          color: $headingColor;
        }
      }
    }
    .list-group-item:first-child,
    .list-group-item:last-child {
      border-radius: 0;
    }
    .courses_new {
      .card {
        background-color: transparent;
        border: 1px solid #f1f1f1;
        .card-body {
          padding: 20px;
          background-color: transparent;
          h5 {
            color: $headingColor;
            text-align: left;
            font-size: 18px;
            font-weight: 700;
          }
          .post_cover {
            padding: 20px 0;
            border-bottom: 2px solid #fff;
            .row {
              margin: 0;
              .col-3,
              .col-9 {
                padding: 0 5px;
                p {
                  text-align: left;
                  color: #81868a;
                  margin: 0;
                  font-size: 12px;
                  a {
                    text-decoration: none;
                    color: $mainColor;
                    font-weight: 600;
                    transition: 0.3s all;
                  }
                  span {
                    color: $headingColor;
                  }
                  .star {
                    display: inline-block;
                    background-color: transparent;
                    font-weight: 400;
                    font-size: 12px;
                    margin: 0;
                    padding: 0 10px 0 0;
                    i {
                      color: #f16101;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

.course-detail-image-slide {
  width: 100%;
  height: 410px;
  background: #ccc;
  background-size: cover;
  background-position: center;
}
