.number {
  position: relative;
  float: left;
  margin-right: -13px;

  font-size: 3em;

  width: 1.1em;
  height: 1.5em;

  &:last-child {
    margin-right: 0px;
  }
}

.number span {
  position: absolute;
  display: block;
  font-weight: bold;
  text-align: center;

  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;

  color: #000000;

  width: 1em;
  height: 1.5em;
  perspective: 7.14em;
}
.community-text{
  text-align: center;

  font-size: 1.5vw;
  font-weight: 400;
  margin-bottom: 0%;
}
.number span:before {
  display: block;
  // height: 0.75em;
  overflow: hidden;

  line-height: 1.52em;

  // border-bottom: 1px solid rgba(0, 0, 0, 0.5);

  border-radius: 2px;

  background: #fff; /* Old browsers */

  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3e3e3e', endColorstr='#2b2b2b',GradientType=0 ); /* IE6-9 */
}

.number span:after {
  display: block;
  height: 0.75em;
  overflow: hidden;
  line-height: 0;

  // border-top: 1px solid rgba(0, 0, 0, 0.5);

  border-radius: 2px;

  transition: color 100ms, background-color 100ms;

  background: #fff; /* Old browsers */
}

.flip .primary:before {
  animation: animate-top 250ms;
  -moz-animation-fill-mode: forwards;

  -webkit-animation: animate-top 250ms;
  -webkit-animation-fill-mode: forwards;

  background: #000000; /* Old browsers */
  background: -moz-linear-gradient(top, #000000 0%, #303030 100%); /* FF3.6+ */
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0%, #000000),
    color-stop(100%, #303030)
  ); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(
    top,
    #000000 0%,
    #303030 100%
  ); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(
    top,
    #000000 0%,
    #303030 100%
  ); /* Opera 11.10+ */
  background: -ms-linear-gradient(top, #000000 0%, #303030 100%); /* IE10+ */
  background: linear-gradient(to bottom, #000000 0%, #303030 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#303030',GradientType=0 ); /* IE6-9 */
}

.flip .primary:after {
  animation: animate-bottom 250ms;
  -moz-animation-fill-mode: forwards;
  animation-delay: 250ms;

  -webkit-animation: animate-bottom 250ms;
  -webkit-animation-fill-mode: forwards;
  -webkit-animation-delay: 250ms;
}

.primary:after {
  opacity: 0;

  transform: translateX(0) translateY(-0.28em) translateZ(0.35em) rotateX(91deg)
    rotateY(0deg) rotateZ(0deg);

  -webkit-transform: translateX(0) translateY(-0.28em) translateZ(0.35em)
    rotateX(91deg) rotateY(0deg) rotateZ(0deg);
}

.primary {
  z-index: 2;
}

.secondary {
  z-index: 1;
}

