#header {
  transition: 0.4s all !important;
  .top_nav {
    transition: 0.4s all !important;
    a.navbar-brand {
      img {
        width: 70%;
      }
    }
    ul.login_user {
      li {
        a {
          color: $mainColor;
          padding: 5px 16px;
          font-size: 18px;
          font-weight: 600;
          transition: 0.4s all;

          &.btn {
            background-color: $mainColor;
            color: #fff;
            border-radius: 25px;
            padding: 3px 16px;
            font-size: 18px;
            transition: 0.4s all;
            &:hover {
              color: $mainColor;
              background-color: transparent;
              border-color: $mainColor;
            }
          }
          &:hover {
            color: #000;
          }
        }
      }
    }
  }
  .main_nav {
    background-color: #fbfbfa;
    padding: 15px 0;
    transition: 0.4s all;
    #primary-menu {
      transition: 0.4s all !important;
      li {
        a {
          color: #000;
          padding: 8px 20px;
          font-weight: 600;
          transition: 0.4s all;
          &:hover {
            color: $mainColor;
          }
        }
        form.form-inline {
          padding: 5px 0 0 0;
          input.form-control {
            background: #f4f4f4;
            border-top-left-radius: 25px;
            font-size: 14px;
            border-bottom-left-radius: 25px;
            &:focus {
              outline: 0;
              box-shadow: none;
              z-index: auto;
              border-color: #000;
            }
          }
          button.btn.btn-submit {
            padding: 10px 15px;
            border-radius: 50%;
            position: relative;
            background-color: $mainColor;
            top: -6px;
            left: -10px;
            z-index: 3;
            color: #fff;
          }
        }

        &.active {
          a {
            color: $mainColor;
            font-weight: 600;
          }
        }
      }
      .dropdown-menu {
        a.active {
          background-color: transparent;
          color: $mainColor;
          font-weight: 600;
        }
      }
    }
  }
  &.sticky-top {
    position: fixed;
    top: 0;
    z-index: 1020;
    width: 100%;
    transition: 0.4s all;
    background-color: #fff;
  }

  /*    .dropdown:hover > .dropdown-menu,.dropdown-submenu:hover > .dropdown-menu {
            display: block;
        }*/
  .dropdown-menu {
    top: 90%;
  }
  /*    .dropdown > .dropdown-toggle:active {
            Without this, clicking will make it sticky
            pointer-events: none;
        }*/
  .dropdown-submenu {
    position: relative;
    a {
      &:focus,
      &:active {
        background-color: transparent;
        color: $mainColor;
      }
    }
  }

  .dropdown-submenu a::after {
    transform: rotate(-90deg);
    position: absolute;
    right: 6px;
    top: 50%;
  }

  .dropdown-submenu .dropdown-menu {
    top: 0;
    left: 100%;
    margin-left: 0.1rem;
    margin-right: 0.1rem;
  }

  .dropdown.nav-item a {
    color: black;
    font-weight: 600;
    cursor: pointer;

    &:hover {
      color: $mainColor;
    }
  }
}

.user-nav-img {
  width: 45px;
  height: 45px;
  background-color: #ccc;
  border-radius: 50px;
  background-size: cover;
  background-position: center;
}

@media (max-width: 1199.98px) {
  .dropdown.nav-item a {
    font-size: 14px;
  }
}

@media (max-width: 1100px) {
  .dropdown .dropdown-toggle.nav-link {
    padding-top: 13px !important;
  }
}

@media (max-width: 991.98px) {
  .dropdown .navbar_all_courses.dropdown-item {
    padding-left: 8px;
  }
}
