.dash_board {
  background-color: #f7f7f7;
  padding: 80px 0;
  padding-top: 200px;
  #sidebar_cover {
    padding-top: 20px;
    transition: 0.4s all;
  }
  .dropify-wrapper {
    border-radius: 50%;
    height: 340px;
  }
  .bg-lighto {
    background-color: $whiteColor;
    border: 1px solid #e6e6e6;
    padding: 30px;
    h4 {
      text-transform: uppercase;
      font-weight: 400;
      margin-bottom: 30px;
    }
    label {
      text-transform: capitalize;
    }
  }
  .dropify-wrapper.has-error .dropify-message .dropify-error,
  .dropify-wrapper.has-preview .dropify-clear {
    display: block;
    position: relative;
    z-index: 100;
    margin: 0 auto;
    left: auto;
    right: auto;
  }
  .sticky-top {
    position: fixed;
    top: 0;
    z-index: 1020;
    margin: 10px 0;

    nav {
      padding: 0 20px;
    }
  }
  ul.data_list {
    li {
      a {
        color: #2b5b44;
        font-size: 14px;
        position: relative;
        &:after {
          width: 1px;
          height: 50%;
          top: 25%;
          position: absolute;
          right: 0;
          background-color: #000;
          content: "";
        }
        &.active {
          font-weight: 600;
          color: $headingColor;
        }
      }
      &:last-child {
        a {
          &:after {
            display: none;
          }
        }
      }
    }
  }
  nav {
    background-color: #011928;
    padding: 0;
    ul {
      border: 0 !important;
      li {
        text-align: center;
        a {
          img {
            display: block;
            margin: 10px auto;
            /*transition: .3s all;*/
          }
          border: 0 !important;
          padding: 10px 0;
          color: $whiteColor;
          &.active {
            color: $mainColor !important;
            background-color: transparent !important;
            img {
              filter: invert(51%) sepia(95%) saturate(5689%) hue-rotate(176deg)
                brightness(99%) contrast(96%);
            }
          }
        }
      }
    }
  }
  #DashboardContent {
    h2 {
      font-size: 40px;
      color: $headingColor;
      font-weight: 700;
      margin: 30px 0;
      text-align: center;
    }
    .sorting_table {
      margin: 0;
      background-color: #fff;
      padding: 0;
      tbody {
        tr {
          td {
            select {
              height: calc(1.5em + 0.75rem + 2px);
              padding: 0.375rem 0.75rem;
            }
            &:first-child {
              width: 10%;
            }
            &:nth-child(2) {
              width: 6%;
              .apply {
                font-size: 14px;
                border-color: #ced4da;
                height: calc(1.5em + 0.75rem + 2px);
                padding: 0.375rem 0.75rem;
                background-color: #fff;
              }
            }
            &:nth-child(3) {
              width: 12%;
            }
            &:last-child {
              width: auto;
              text-align: center;
              .w-20 {
                width: 30%;
                margin: 0 auto 0 auto;
              }
            }
          }
        }
      }
    }
    #blogTable {
      margin-top: 0 !important;
      background-color: #fff;
      th {
        font-weight: 500;
      }
      tbody {
        td {
          &.sorting_1 {
            text-align: center;
          }
          &:nth-child(2) {
            font-weight: 600;
            font-size: 18px;
            color: #245e4c;
          }
        }
      }
    }
  }
}
