.login-container {
  .bg {
    background-color: #fafafa;
    form {
      .margin {
        margin-top: 40px;
      }

      .social {
        width: 50%;
        margin: 0 auto 80px;
      }
    }
    .round {
      display: block;
      position: relative;
      padding-left: 35px;
      margin-bottom: 12px;
      cursor: pointer;
      font-size: 16px;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
    }

    /* Hide the browser's default checkbox */
    .round input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
    }

    /* Create a custom checkbox */
    .checkmark {
      position: absolute;
      top: 0;
      left: 0;
      height: 25px;
      width: 25px;
      background-color: #eee;

      border-radius: 15px;
    }

    /* On mouse-over, add a grey background color */
    .round:hover input ~ .checkmark {
      background-color: #ccc;
    }

    /* When the checkbox is checked, add a blue background */
    .round input:checked ~ .checkmark {
      background-color: #2196f3;
    }

    /* Create the checkmark/indicator (hidden when not checked) */
    .checkmark:after {
      content: "";
      position: absolute;
      display: none;
    }

    /* Show the checkmark when checked */
    .round input:checked ~ .checkmark:after {
      display: block;
    }

    /* Style the checkmark/indicator */
    .round .checkmark:after {
      left: 10px;
      top: 7px;
      width: 5px;
      height: 10px;
      border: solid white;
      border-width: 0 3px 3px 0;
      transform: rotate(45deg);
    }

    .register-link {
      text-align: center;

      label {
        font-weight: 500;
      }

      a {
        color: #81868a;

        i {
          color: #ffffff;
          transition: 0.3s all;
          -webkit-transition: 0.3s all;
          -moz-transition: 0.3s all;
          -ms-transition: 0.3s all;
          -o-transition: 0.3s all;

          &.fa-google-plus-g {
            background-color: #df4b38;
            padding: 15px 12px;
            border-radius: 40px;
            font-size: 28px;
            -webkit-border-radius: 40px;
            -moz-border-radius: 40px;
            -ms-border-radius: 40px;
            -o-border-radius: 40px;
            border: 1px solid #df4b38;

            &:hover {
              background-color: #ffffff;
              color: #df4b38;
            }
          }

          &.fa-facebook-f {
            background-color: #3b5998;
            padding: 15px 23px;
            border-radius: 40px;
            font-size: 28px;
            border: 1px solid #3b5998;
            -webkit-border-radius: 40px;
            -moz-border-radius: 40px;
            -ms-border-radius: 40px;
            -o-border-radius: 40px;
            &:hover {
              background-color: #ffffff;
              color: #3b5998;
            }
          }
        }
      }

      a.bold {
        font-weight: 600;
        color: #000000;
        text-decoration: none;
        border-bottom: 1px solid;
      }
    }

    .img {
      padding: 0;

      img {
        width: 100%;
        height: 100%;
      }
    }
  }

  //Input Fields
  input {
    border: none;
    background-color: #ffffff;
    box-shadow: none;
    padding: 28px 14px;
  }

  // --- OR ---
  .or {
    font-weight: 500;
    display: block;
    text-transform: uppercase;
    position: relative;
    text-align: center;
    &:before {
      content: "";
      position: absolute;
      top: 12px;
      left: 0;
      width: 45%;
      height: 1px;
      background-color: #e1e2e4;
    }
    &:after {
      content: "";
      position: absolute;
      top: 12px;
      right: 0;
      width: 45%;
      height: 1px;
      background-color: #e1e2e4;
    }
  }
  //form submit Btn
  .btn {
    display: block;
    background-color: #00acf0;
    color: #fff;
    transition: 0.4s all;
    padding: 10px 50px;
    font-weight: 600;
    border-radius: 25px;
    margin: 20px auto;
    display: block;
    width: 75%;
    &:hover {
      background-color: #fff;
      color: #00acf0;
      border-color: #00acf0;
    }
  }
}

.popup-form-wrapper {
  margin: 38px;
}

.social-login-wrapper {
  width: 250px;
  margin: auto;
}
